import React, { useContext } from 'react';
import { Link, NavLink, useHistory } from 'react-router-dom';
import { AuthContext } from '../../auth/AuthContext';
import { types } from '../../types/types';

export const Navbar = () => {
	const { session, dispatch } = useContext(AuthContext);
	const history = useHistory();
	const handleLogout = () => {
		dispatch({ type: types.logout });
		history.replace('/login');
	};
	return (
		<nav className="navbar navbar-expand-sm navbar-dark bg-dark">
			<div className="container">
				<Link className="navbar-brand" to="/">
					Heroes App
				</Link>

				<div className="collapse navbar-collapse justify-content-between">
					<div className="navbar-nav">
						<NavLink activeClassName="active" className="nav-item nav-link" exact to="/marvel">
							Marvel
						</NavLink>

						<NavLink activeClassName="active" className="nav-item nav-link" exact to="/dc">
							DC
						</NavLink>
						<NavLink activeClassName="active" className="nav-item nav-link" exact to="/search">
							Search
						</NavLink>
					</div>
					<div className="d-flex">
						<ul className="navbar-nav ml-auto">
							{session.sesion && <span className="nav-item nav-link text-warning">{session.nombre}</span>}
							<button className="btn btn-outline-warning" onClick={handleLogout}>
								Logout
							</button>
						</ul>
					</div>
				</div>
			</div>
		</nav>
	);
};
